
import { Link } from 'react-router-dom';

import Banner_IMG from '../../../../Assets/Icons/Logo/logo.png'







function Banner() {



  
    return (
 
        <div className="nav_logo">
            <Link to="/boost_react" >
                <img src={Banner_IMG} alt="" />
            </Link>
        </div>
    );
  }
  
  export default Banner;