import React from 'react';
import '../../Styles/Feature/Feature.css';
import Content from '../../Components/Content/Content';
import Offer from '../../Components/Offer/Offer'
import Intros from '../../Data/Feature/Intro.json'
import H1 from '../../Components/Header/H1'
import Previews from '../../Data/Feature/preview.json';
import Offers1 from '../../Data/Feature/Offer1.json'
import Offers2 from '../../Data/Feature/Offer2.json'

import intro_IMG from '../../Assets/Images/demo_phone.png'



import preview_task from '../../Assets/Images/Preview/boost_task.png'
import preview_chat from '../../Assets/Images/Preview/boost_chat.png'
import preview_profile from '../../Assets/Images/Preview/boost_profile.png'










function Feature() {

   const Preview_IMG = [ preview_task, preview_chat ,preview_profile ] 
   
    return (
        <>

        <Content   {...Intros}  image={intro_IMG} />

    <div className="feature_offer_section">
         <H1  H1Size={"medium"} page={"feature"} section={"offer"} />
         <div className="feature_offers">
           <div className="feature_offer_box">
            {Offers1.map(offer1 =>{
              return (
                 <Offer key={offer1.id}  {...offer1} />
              )
               })}
           </div>
           <div className="feature_offer_box">
             {Offers2.map(offer2 =>{
                return (
                 <Offer  key={offer2.id} {...offer2} />
               )
                })}
           </div>
        </div>
    </div>

        {Previews.map(preview =>{
            return (
              <Content key={preview.id}  {...preview} image={Preview_IMG[preview.id -1]} />
            )
             })}

        </>
    );
  }
  
  export default Feature;
  