import React from 'react';
import '../../Styles/About/About.css';
import Content from '../../Components/Content/Content';
import Member from '../../Components/Member/Member'
import H1 from '../../Components/Header/H1'
import H3 from '../../Components/Header/H3'
import Intros from '../../Data/About/Intro.json'
import Goals from '../../Data/About/Goal.json';
import Founders from '../../Data/About/Founder.json';
import Staffs from '../../Data/About/Staff.json';

import intro_IMG from '../../Assets/Images/demo_full.png'


import goal_rank from '../../Assets/Images/Goals/rank.png'
import goal_shape from '../../Assets/Images/Goals/shape.png'


import founder_minh from '../../Assets/Images/Members/ava_Minh.jpg'
import founder_hoan from '../../Assets/Images/Members/ava_Hoan.jpg'


import staff_thanh from '../../Assets/Images/Members/ava_Thanh.jpg'
import staff_hieu from '../../Assets/Images/Members/ava_Hieu.jpg'
import staff_hoang from '../../Assets/Images/Members/ava_Hoang.jpg'
import staff_lam from '../../Assets/Images/Members/ava_Lam.jpg'
import staff_vuong from '../../Assets/Images/Members/ava_Vuong.jpg'

function About() {


    const goal_IMG = [ goal_rank, goal_shape]

    const founder_IMG = [  founder_minh, founder_hoan ]
    const staff_IMG = [ staff_thanh, staff_hieu, staff_hoang, staff_lam, staff_vuong]

    return (
     <>
        <Content  {...Intros} image={intro_IMG} />

        {Goals.map(goal =>{

            return (
                <Content  key={goal.id} {...goal} image={goal_IMG[goal.id -1]} />
            )
            
               })}
        <div className="about_member_section">
            <H1  H1Size={"medium"} page={"about"} section={"member"} />
            <H3 H3ID={1} H3Size={"small"} page={"about"} section={"member"} />
            <div className="about_member_founder_section">

               {Founders.map(founder =>{
                      return (
                       <Member key={founder.id}  {...founder} image={founder_IMG[founder.id - 1]} />
                      )
                 })}
            </div>

            <H3 H3ID={2} H3Size={"small"} page={"about"} section={"member"} />
            <div className="about_member_staff_section">

                  {Staffs.map(staff =>{
                      return (
                       <Member key={staff.id}  {...staff} image={staff_IMG[staff.id -1]}/>
                       )
                    })}
             </div>
        </div>

     </>
    );
  }
  
  export default About;
  