import React from 'react';
import '../../Styles/Career/Career.css';
import Content from '../../Components/Content/Content';
import Comment from '../../Components/Comment/Comment'

import Intros from '../../Data/Career/Intro.json';
import Comments from '../../Data/Career/Comment.json'

import intro_IMG from '../../Assets/Images/demo_full.png'

import comment_hieu from '../../Assets/Images/Comments/ava_Hieu.jpg'
import comment_thanh from '../../Assets/Images/Comments/ava_Thanh.jpg'
import comment_hoang from '../../Assets/Images/Comments/ava_Hoang.jpg'



function Career() {


     const comment_IMG = [ comment_hieu, comment_thanh ,comment_hoang]
    return (
        <>

        <Content {...Intros} image={intro_IMG} />

        <div className="career_comment_section">
           {Comments.map(comment => {
               return (
                <Comment key={comment.id} {...comment} image={comment_IMG[comment.id -1]} />
             )
               })}
        </div>

        </>
    );
  }
  
  export default Career;
  